.flip-back{
	position:absolute;
	top:20px;
	right:20px;
	width:30px;
	height:30px;
	cursor:pointer;
}
.cx, .cy{
	background:#d2d5dc;
	position:absolute;
	width:0px;
	top:15px;
	right:15px;
	height:3px;
transition: all 250ms ease-in-out;
}
.flip-back:hover .cx, .flip-back:hover .cy{
	background:#979ca7;
transition: all 250ms ease-in-out;
}
.cx.s1, .cy.s1{	
	right:0;	
	width:30px;
transition: all 100ms ease-out;
}
.cy.s2{ 
	-webkit-transform: rotate(50deg); 
	transform: rotate(50deg);
transition: all 100ms ease-out;
}
.cy.s3{ 
	-webkit-transform: rotate(45deg); 
	transform: rotate(45deg);
transition: all 100ms ease-out;
}
.cx.s1{	
	right:0;	
	width:30px;
transition: all 100ms ease-out;
}
.cx.s2{ 
	-webkit-transform: rotate(140deg); 
	transform: rotate(140deg);
transition: all 100ms ease-out;
}
.cx.s3{ 
	-webkit-transform: rotate(135deg); 
	transform: rotate(135deg);
transition: all 100ms ease-out;
}
.carousel{
	width:315px;
	height:500px;
	overflow:hidden;
	position:relative;
}
.carousel ul{
	position:absolute;
	top:0;
	left:0;
}
.carousel li{
	width:315px;
	height:500px;
	float:left;
	overflow:hidden;	
}
.carousel img{
	margin-top: -22px;
	width: 110%;
}
.arrows-perspective{
	width:315px;
	height:55px;
	position: absolute;
	top: 218px;
	-webkit-transform-style: preserve-3d;
	        transform-style: preserve-3d;
    transition: -webkit-transform 5s;
    transition: transform 5s;
    transition: transform 5s, -webkit-transform 5s;
	-webkit-perspective: 335px;
	        perspective: 335px;
}
.carouselPrev, .carouselNext{
	width: 50px;
	height: 55px;
	background: #ccc;
	position: absolute;	
	top:0;
	transition: all 200ms ease-out; 
	opacity:0.9;
	cursor:pointer;
}
.carouselNext{
	top:0;
	right: -26px;
	-webkit-transform: rotateY( -117deg );
              transform: rotateY( -117deg );
			  transition: all 200ms ease-out; 			

}
.carouselNext.visible{
		right:0;
		opacity:0.8;
		background: #fff;
		-webkit-transform: rotateY( 0deg );
              transform: rotateY( 0deg );
			  transition: all 200ms ease-out; 
}
.carouselPrev{		
	left:-26px;
	top:0;
	-webkit-transform: rotateY( 117deg );
              transform: rotateY( 117deg );
			  transition: all 200ms ease-out; 

}
.carouselPrev.visible{
		left:0;
		opacity:0.8;
		background: #fff;
		-webkit-transform: rotateY( 0deg );
              transform: rotateY( 0deg );
			  transition: all 200ms ease-out; 
}
.carousel .x, .carousel .y{
	height:2px;
	width:15px;
	background:#5ff7d2;
	position:absolute;
	top:31px;
	left:17px; 
	-webkit-transform: rotate(45deg); 
	transform: rotate(45deg);	
}
.carousel .x{ 	
	-webkit-transform: rotate(135deg); 
	transform: rotate(135deg);		
	top:21px;
}
.carousel .carouselNext .x{ 	
	-webkit-transform: rotate(45deg); 
	transform: rotate(45deg);		
}
.carousel .carouselNext .y{ 	
	-webkit-transform: rotate(135deg); 
	transform: rotate(135deg);		
}
